import { Component, OnInit } from '@angular/core';

import { ActivatedRoute, Router} from '@angular/router'
import { BlogServiceService } from '../blog-service.service';
import { BlogHttpService } from '../blog-http.service';


//go back functionality
import { Location} from '@angular/common';

@Component({
  selector: 'app-blog',
  templateUrl: './blog.component.html',
  styleUrls: ['./blog.component.css'],
  providers : [Location]
})
export class BlogComponent implements OnInit {

  public currentBlog;

 

  constructor( private _route : ActivatedRoute , private router : Router,  public BlogHttpService: BlogHttpService, private Location : Location) { 
    console.log("Constructor is called");
  }

  ngOnInit() {
    console.log("OnInit is called");
    let myBlogId = this._route.snapshot.paramMap.get('blogId');
    console.log(myBlogId);


    //first technique when the data was in localhost
    // this.currentBlog = this.BlogHttpService.getSingleBlogInformation(myBlogId);
    // console.log(this.currentBlog);




    this.BlogHttpService.getSingleBlogInformation(myBlogId).subscribe(

      data => {
        console.log("Success ");
        console.log(data);
        this.currentBlog = data["data"];
      },
      error => {
        console.log("SOme Error");
        console.log(error.errorMessage);
      }

    )

  }

  public deleteThisBlog() : any {
    this.BlogHttpService.deleteBlog(this.currentBlog.blogId).subscribe(
      data  => {
        console.log("Deleted SUccessfully");
        alert ("Blog Deleted");
        setTimeout(() => {
          this.router.navigate(['/home']);}, 1000)
        },
        error => {
          console.log("Some Error is occured");
          alert("Error Occured");
        }

        )
      }

      public goBackToPreviousPage(): any {
        this.Location.back();
      }

    
  }

