import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

//router model used for setting up the application level route
import { RouterModule} from '@angular/router';

//toast
//import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
// import { ToastrModule } from 'ngx-toastr';
// import { CommonModule } from '@angular/common';
// import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

//import { ErrorHandler} from 'errno';

import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';
import { BlogComponent } from './blog/blog.component';
import { BlogCreateComponent } from './blog-create/blog-create.component';
import { BlogEditComponent } from './blog-edit/blog-edit.component';
import { AboutComponent } from './about/about.component';
import { NotFoundComponent } from './not-found/not-found.component';
import { BlogServiceService } from './blog-service.service';
import {BlogHttpService} from './blog-http.service';

//import forms
import { FormsModule} from '@angular/forms';
import { HttpClientModule} from '@angular/common/http';
@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    BlogComponent,
    BlogCreateComponent,
    BlogEditComponent,
    AboutComponent,
    NotFoundComponent
  ],
  imports: [
    BrowserModule,
   // CommonModule,
    FormsModule,
    //CommonModule,
    HttpClientModule,
    //BrowserAnimationsModule, 
    //ToastrModule.forRoot(),
    //router module forRoot mothod to declare the possible routes in application
    RouterModule.forRoot([
      {path: 'home',component:HomeComponent},
      {path: '' ,redirectTo:'home', pathMatch: 'full'},
      {path: 'about',component:AboutComponent},
      {path: 'create',component: BlogCreateComponent},
      {path: 'edit/:blogId',component:BlogEditComponent},
      {path:'blog/:blogId',component:BlogComponent},
      {path: '**',component:NotFoundComponent}

    ])
  ],
  providers: [BlogServiceService,BlogHttpService],
  bootstrap: [AppComponent]
})
export class AppModule { }
